.tcp__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.tcp__footer-header {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.tcp__footer-header h1 {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
}

.tcp__footer-links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}

.tcp__footer-links img {
    width: 50%;
}

.tcp__footer-links div {
    width: 250px;
    margin: 1rem;
}

.tcp__footer-links-items {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.tcp__footer-links-items h4 {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 22px;
    
    color: var(--color-text);
    
    margin-bottom: 1rem;
}

.tcp__footer-links-items p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 22px;
    
    color: var(--color-text);
    
    margin: 0.5rem 0;
    cursor: pointer;
}

.tcp__footer-copyright {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 22px;
    
    color: #FFF;
    
    margin-top: 2rem;
    text-align: center;
}