.tcp__header {
    display: flex;
}

.tcp__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.tcp__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 100px;
}

.tcp__header-content h2 {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 18px;
    font-style: italic;
    color: var(--color-text);

    margin-left: 3rem;
}

.tcp__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 40px;

    color: var(--color-text);
    margin-top: 1.5rem;
}

.tcp__header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;

    display: flex;
}

.tcp__header-content_input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: var(--color-text-white);
    border-radius: 6.5px 0px 0px 6.5px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
}

.tcp__header-content_input button {
    background: #FABF12;
    border-radius: 0px 6.5px 6.5px 0px;
    transform: matrix(1, 0, 0, 1, 0, 0);

    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;

    border: none;
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
}

.tcp__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.tcp__header-content__people img {
    width: 250px;
    height: 52px;
}

.tcp__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    
    color: var(--color-text);
}

.tcp__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tcp__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .tcp__header {
        flex-direction: column;
    }
    .tcp__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 700px) {
    .tcp__header h1{
        font-size: 35px;
        line-height: 60px;
    }

    .tcp__header h2{
    margin-left: 0;
    }
    .tcp__header p{
        font-size: 16px;
        line-height: 24px;
    }

    .tcp__header-content__people {
        flex-direction: column;
    }

    .tcp__header-content__people p {
        margin: 0;
        font-size: 14px;
        line-height: 20px;
    }
}