.tcp__tongquan {

    display: flex;
    flex-direction: column;
    align-items: center;
    
    max-width: 1200px;
    padding: 2rem;

    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(31, 102, 240, 0.37) 88.54%);
    filter: drop-shadow(0px 6.10325px 16.7839px rgba(0, 0, 0, 0.3));
    border: 5px;
    border-color: antiquewhite;
    box-sizing: border-box;
    border-radius: 40px;
}


.tcp__tongquan-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.tcp__tongquan-header h1 {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 32px;
    line-height: 50px;
}

.tcp__tongquan-header p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: var(--color-text);
}

.tcp__tongquan-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    margin-top: 2rem;
}

@media screen and (max-width: 850px) {
    .tcp__tongquan-header {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0;
    }
    
    .tcp__tongquan-header h1 {
        margin: auto;
    }

    .tcp__tongquan-header p {
        margin-top: 1rem;
    }
    .tcp__tongquan-container {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
    }

}

@media screen and (max-width: 650px) {
    .tcp__tongquan-header h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__tongquan-header h1 {
        font-size: 24px;
        line-height: 36px;
    }
}