.tcp__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.tcp__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.tcp__navbar-links_logo {
    margin-right: 2rem;
}

.tcp__navbar-links_logo img {
    width: 80px;
    height: 60px;
}

.tcp__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.tcp__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.tcp__navbar-links_container p,
.tcp__navbar-sign p,
.tcp__navbar-links_container p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 35px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.tcp__navbar-sign button,
.tcp__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: var(--color-text);
    left: 1154px;
    top: 36px;

    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 19px;

    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff22 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(9, 255, 0, 0.64) 319.41deg, #00ff00 360deg);
    box-shadow: inset -1px -3px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    border: 0;
    outline: none;   
    cursor: pointer;
}

.tcp__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.tcp__navbar-menu svg {
    cursor: pointer;
}

.tcp__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.tcp__navbar-links_container p {
    margin: 1rem 0;
}

.tcp__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .tcp__navbar-links_container {
        display: none;
    }

    .tcp__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .tcp__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .tcp__navbar {
        padding: 2rem;
    }
    .tcp__navbar-sign {
        display: none;
    }
    .tcp__navbar-menu_container {
        top: 10px;
    }
    .tcp__navbar-menu_container-links-sign {
        display: block;
    }
}