.gradient__text-green {
    background: linear-gradient(30deg, #a99904 -13.11%, #04A950 63.91%, #00561880 147.63%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;

    text-shadow: 5px 5px 10px #adacaca4;
}

.gradient__text-yellow {
  background: linear-gradient(30deg, #FABF12 -13.11%, #FABF12 63.91%, #fff 147.63%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;

  text-shadow: 5px 5px 10px #adacaca4;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.section__center {
  margin: auto;
}

.scale-up-tr {
	-webkit-animation: scale-up-tr 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-tr 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 1s ease-out 0s;
  -moz-transition: all 1s ease-out 0s;
  -ms-transition: all 1s ease-out 0s;
  -o-transition: all 1s ease-out 0s;
  transition: all 1s ease-out 0s;

  width: 120px;

  font-family: var(--font-family);
  font-size: 14px;

  color: var(--color-text);
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  width: 300px;
  bottom: 100%;
}

/* Show the tooltip text when mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  -webkit-opacity: 1;
  -moz-opacity: 1;
  opacity: 1;
}

