.tcp__features_linhvuc {
    display: flex;
    flex-direction: row;
    justify-items: flex-start;
    align-items: center;
    padding: 1rem;
}

.tcp__features_linhvuc-container__feature-title {
    min-width: 180px;
    display: flex;
    flex: 0.5;
}

.tcp__features_linhvuc-container__feature-title div {
    width: 20px;
    height: 3px;
    background: linear-gradient(5.12deg, #FFE600 -13.11%, #FFD84F 63.91%, #FFFFFF 147.63%);
}

.tcp__features_linhvuc-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    
    margin: 0.5rem 2rem 0 0;
    color: var(--color-text);
}

.tcp__features_linhvuc-container__feature-text {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    
    color: var(--color-text);
}