.tcp__linhvuc {
    display: flex;
    flex-direction: column;
}

.tcp__linhvuc-header {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.tcp__linhvuc-header h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 100px;
}

.tcp__linhvuc-content {
    display: flex;
    flex-direction: row;
    margin: auto;
}

.tcp__linhvuc-content-image {
    display: flex;
    justify-content: center;
    align-items: center;
}

.tcp__linhvuc-content-image img {
    width: 100%;
    height: 100%;
}

.tcp__linhvuc-content-feature {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

@media screen and (max-width: 1200px) {
    .tcp__linhvuc-header {
        margin: auto;
    }
    .tcp__linhvuc-content {
        flex-direction: column;
    }

    .tcp__linhvuc-content-feature {
        margin: 0;
    }
}

@media screen and (max-width: 700px) {
    .tcp__linhvuc-header h1 {
        font-size: 35px;
        line-height: 60px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__linhvuc-header h1 {
        font-size: 28px;
        line-height: 50px;
    }
}