.tcp__tintuc {
    display: flex;
    flex-direction: column;
}

.tcp__tintuc-header {
    display: flex;
    flex-direction: column;
    padding-bottom: 1rem;
}

.tcp__tintuc-header h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 100px;
}

.tcp__tintuc-header-items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}

.tcp__tintuc-header-text {
    max-width: 600px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: var(--color-text);
}

.tcp__tintuc-header-link {
    display: flex;
    justify-content: flex-end;
    min-width: 80px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    
    color: var(--color-text);
    cursor: pointer;
}

.tcp__tintuc-pads {
    display: flex;
    flex-direction: row;
    margin: auto;
    z-index: 0;
}
    
@media screen and (max-width: 1400px) {
    .tcp__tintuc-pads {
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 1200px) {
    .tcp__tintuc-header h1 {
        margin: auto;
    }
}

@media screen and (max-width: 700px) {
    .tcp__tintuc-header h1 {
        font-size: 35px;
        line-height: 60px;
    }
}

@media screen and (max-width: 410px) {
    .tcp__tintuc-header h1 {
        font-size: 28px;
        line-height: 50px;
    }
}