.tcp__banchothue {
    display: flex;
    flex-direction: column;
}

.tcp__banchothue-items {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.tcp__banchothue-image img {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.tcp__banchothue-content {
    margin-left: 2rem;
}

.tcp__banchothue-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 100px;
}

.tcp__banchothue-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    color: var(--color-text);
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}

.tcp__banchothue-vi {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
    padding: 1rem;

    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    border: 1.5px solid #000000;
    box-shadow: 0px 6px 5.47887px rgba(0, 0, 0, 0.25);
    border-radius: 15px;

}

.tcp__banchothue-vi h2 {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 16px;
    line-height: 40px;
    font-style: italic;
    color: var(--color-text);
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-right: 1.5rem;
}

.tcp__banchothue-vi button {
    background: conic-gradient(from 177.26deg at 42.79% -20.42%, #00ff6a 0deg, #EAB43A 63.06deg, #EAB43A 272.56deg, rgba(0, 255, 34, 0.64) 319.41deg, #00ff2a 360deg);
    box-shadow: 0px 5.5px 4px rgba(0, 0, 0, 0.55), inset 0px 5.5px 5.5px rgba(255, 255, 255, 0.63);
    border-radius: 55px;

    font-family: var(font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;

    border: none;
    padding: 0.5rem 2rem;
    color: #fff;
    cursor: pointer;

}

.tcp__banchothue-link {
    display: flex;
    justify-content: flex-end;
    min-width: 80px;

    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    
    margin-bottom: 2rem;
    
    color: var(--color-text);
    cursor: pointer;
}

.pad h4 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    
    color: var(--color-text);
}

@media screen and (max-width: 1150px) {
    .tcp__banchothue-items {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .tcp__banchothue-content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

@media screen and (max-width: 750px) {
    .tcp__banchothue-content h1 {
        font-size: 28px;
        line-height: 40px;
    }

    .tcp__banchothue-vi {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .tcp__banchothue-vi h2{
        font-size: 12px;
        margin: 0.5rem;
    }
    .tcp__banchothue-vi button {
        font-size: 16px;
        padding: 0.5rem 1rem;
    }
}